import React, { useEffect, useState, useRef, } from 'react';
import selectBOX from '../../data/SelectBox.js';
import  LatLng  from '../../data/KoreaLatLng.js';
import { useDispatch } from 'react-redux';
import { AddLatLng } from '../../store.js';

export function RealEstate() {  // 컴포넌트.

  const [selectedSido, setSelectedSido] = useState(''); // 선택한 시/도
  const [selectedGugun, setSelectedGugun] = useState(''); // 선택한 구/군

  let dispatch = useDispatch();

  const selectStyle = {
    width: '200px',
    height: '40px',
    fontSize: '16px',
    fontFamily: 'Arial, sans-serif',
    borderRadius: '10px', // 모서리를 둥글게 조정
    // 다른 스타일 속성들을 여기에 추가할 수 있습니다.
  };

  useEffect(() => {
    selectBOX();  // 시/도/군/구 selectBOX 생성함수를 컴포넌트가 로드 되자마자 실행해준다.
  }, []);

  useEffect(() => {
    // 선택한 시/도와 구/군에 따라 위도와 경도 값을 설정하는 로직을 추가
    if (selectedSido && selectedGugun) {
      let {latitude,longitude} = LatLng(selectedSido,selectedGugun);
      dispatch(AddLatLng({ lat: latitude, lng: longitude }));
    } else {
      // 다른 시/도 및 구/군에 대한 경우 여기에 적절한 위도와 경도 값을 설정
      dispatch(AddLatLng({ lat: null, lng: null }));
    }
  }, [selectedSido, selectedGugun]);

  // 시/도 선택 박스 변경 이벤트 핸들러
  const handleSidoChange = (e) => {
    setSelectedSido(e.target.value); // 선택한 시/도 업데이트
    setSelectedGugun(null);
  };

  // 구/군 선택 박스 변경 이벤트 핸들러
  const handleGugunChange = (e) => {
    setSelectedGugun(e.target.value); // 선택한 구/군 업데이트
  };

  return (
    <div>
      <select
        name="sido1"
        id="sido1"
        style={selectStyle}
        value={selectedSido}
        onChange={handleSidoChange} // 시/도 변경 핸들러 연결
      ></select>
      <>  </>
      <select
        name="gugun1"
        id="gugun1"
        style={selectStyle}
        value={selectedGugun}
        onChange={handleGugunChange} // 구/군 변경 핸들러 연결
      ></select>
    </div>
  );
}

export default RealEstate;

